import React from "react";
import { CardColumns } from "react-bootstrap";
import CodeCard from "./CodeCard";
import BrowserSupportCard from "./BrowserSupportCard";
import { install, simpleTest, runningTest } from "../snippets";

const CodeCardContainer = () => (
  <CardColumns>
    <CodeCard
      cardTitle={install.title}
      codeSnippet={{
        code: install.code,
        language: install.language
      }}
    />
    <CodeCard
      cardTitle={simpleTest.title}
      codeSnippet={{
        code: simpleTest.code,
        language: simpleTest.language
      }}
    />
    <CodeCard
      cardTitle={runningTest.title}
      codeSnippet={{
        code: runningTest.code,
        language: runningTest.language
      }}
    />
    <BrowserSupportCard />
  </CardColumns>
);

export default CodeCardContainer;
